import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NotificationService } from 'src/app/modules/main/services/notification.service';
import { IUserLogin } from 'src/app/shared/interfaces/i-auth';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
// test: string
email!:string
password!:string
userData!: IUserLogin

logInForm: FormGroup;
hide = true;
constructor(private authService: AuthService, 
            private router: Router, 
            private formB: FormBuilder,
            private notiSvc: NotificationService) {
  this.buildForm();
}

ngOnInit(): void {
}

  logIn(): void{
    if(this.logInForm.invalid){
      this.notiSvc.openSnackBar("Favor de introducir los datos correctamente", 2000)
      return ;
    }
    this.setAuthData()
    this.authService.onLogIn(this.userData).subscribe( () => {
        console.log("pog")
        this.router.navigate(['panel/empresas']);
      }, error => {
        console.log(error);
        this.notiSvc.openSnackBar("Credenciales Incorrectas", 2000)
      });
  }

  register(): void{
      this.router.navigate(['auth/register']);
  }

  buildForm(): void {
    this.logInForm = this.formB.group({
      email: ['', Validators.required],
      password: ['', Validators.required]
    });
  }

  setAuthData(): void{
    this.userData = {
      email: this.logInForm.get('email')?.value,
      password: this.logInForm.get('password')?.value
    };
  }
}
